import React, { forwardRef } from "react";

const Header = forwardRef((props, refHead) => {
  return (
    <header ref={refHead}>
      <div className="container header-container">
        <h1 className="h1 rise">Exploring STEM Horizons</h1>
        <p className="h3 rise subheading">Innovation is the key to unlocking the future.</p>
      </div>
    </header>
  );
});

export default Header;